import echarts from "echarts";
import {reportData,} from '../../../server/report'
export default {
  name: "southChart",
  data() {
    return {
      dataName:[],//总的数据
      valueList:[],//图表数据
      titles:[],//分割的数据
       title:[],//标题
          totalList:[],//总的数据
          nameList:[],//图表数据
    };
  },
  created(){
    this.getData()
    this.init()
    // this.getLine()
  },
  methods: {
    init() {
        const self = this;//因为箭头函数会改变this指向，指向windows。所以先把this保存
        setTimeout(() => {
          window.onresize = function() {
              self.chart = echarts.init(document.getElementById("chart_example"));
              self.chart.resize();
          }
        },20)
    },
    getData(){
      reportData({
        dateType:'D'
      }).then(e=>{
        if(e.data.code==200){
          this.dataName=e.data.data
          for (var key in this.dataName) {
              this.title.push(key)
              this.valueList.push(this.dataName[key])
          }
          this.titles=this.title[0].split('-')
          this.valueList[0].map(item => this.nameList.push(item.organizationName))
          this.valueList[0].map(item=> this.totalList.push(item.total))
          this.$nextTick(()=> {
              this.loadEchart()
              // this.percentEchart()
          })
        }
      })
    },
    // getLine(){
    //   reportLine().then(e=>{
    //     if(e.data.code==200){
    //       this.dataName=e.data.data
    //       for (var key in this.dataName) {
    //           this.title.push(key)
    //           this.valueList.push(this.dataName[key])
    //       }
    //       this.titles=this.title[0].split('-')
    //       this.valueList[0].map(item => this.nameList.push(item.organizationName))
    //       this.valueList[0].map(item=> this.totalList.push(item.total))
    //       this.$nextTick(()=> {
    //           this.loadEchart()
    //           this.percentEchart()
    //       })
    //     }
    //   })
    // },
    //柱状图汇总
    loadEchart() {
        this.myChart = echarts.init(document.getElementById("chart_example"));
        this.myChart.setOption({
      tooltip: {
        trigger: "axis"
      },
      legend: {
        // data: this.nameList
      },
      grid: {
        left: '3%',
        // right: '4%',
        bottom: '3%',
        containLabel: true
      },
      xAxis: {
        type: "value",
        // boundaryGap: [0.2, 0.2],
        max: 1400,
        min: 0,
        splitNumber:7,
        axisLine:{
            show:false
        },
        axisTick:{
            show:false
        },
      },
      yAxis: [
        {
          type: "category",
          boundaryGap: [0.2, 0.2],
          data:this.nameList,
          scale: true,
          axisTick:{
              show:false
            },
        },
      ],
      series: [
        {
          name: '',
          type: 'bar',
          data: this.totalList,
          // data:[200],
          label: {
                normal: {
                    show: true,
                    position: 'right'
                }
            },
             itemStyle:{
              normal:{
                  color:'#EB9F4B'
              }        
            }
        },
      ]
      });
    },
    //员工占比
//     percentEchart() {
//         this.myChartPersend = echarts.init(document.getElementById("persendChart"));
        
